import React from "react"
import { graphql } from "gatsby"
import { Link } from "gatsby-theme-material-ui"
import { makeStyles } from "@material-ui/core/styles"
import Grid from "@material-ui/core/Grid"
import Pagination from "@material-ui/lab/Pagination"
import PaginationItem from "@material-ui/lab/PaginationItem"

import SEO from "../components/SEO"
import Layout from "../components/Layout"
import Post from "../components/Post"

const useStyles = makeStyles(theme => ({
  grid: {
    marginBottom: theme.spacing(2),
  },
}))

const PostIndex = ({ data, pageContext, location }) => {
  const classes = useStyles()
  const siteTitle = data.site.siteMetadata.title
  const posts = data.allMdx.edges
  const { currentPage, numPages, pathPrefix } = pageContext

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={siteTitle}
        keywords={[`blog`, `gatsby`, `javascript`, `react`]}
      />
      <Grid
        container
        className={classes.grid}
        spacing={2}
        justify="space-evenly"
        alignItems="center"
      >
        {posts.map(({ node }, i) => {
          return (
            <Grid key={i} item>
              <Post node={node} />
            </Grid>
          )
        })}
      </Grid>
      <Grid container justify="center">
        <Pagination
          color="primary"
          shape="rounded"
          count={numPages}
          page={currentPage}
          showFirstButton
          showLastButton
          renderItem={item => (
            <PaginationItem
              component={Link}
              to={`/${pathPrefix}/${item.page === 1 ? "" : `${item.page}`}`}
              {...item}
            />
          )}
        />
      </Grid>
    </Layout>
  )
}

export default PostIndex

export const pageQuery = graphql`
  query postIndexQuery($skip: Int!, $limit: Int!, $filter: String!) {
    site {
      siteMetadata {
        title
      }
    }
    allMdx(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
      filter: { fields: { type: { eq: $filter } } }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
